.App {
  text-align: center;
}

/* Periodic table */

.periodic-table {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 3 1;
      flex: 3 1;
  -ms-flex-direction: column;
      flex-direction: column;
  /* background-color: navajowhite; */
}

.periodic-table-period {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.periodic-element-filled {
  border: 1px solid black;
}

.periodic-element {
  -ms-flex: 1 1;
      flex: 1 1;
  margin: 1px;
  -ms-flex-preferred-size: auto;
      flex-basis: auto;
  min-width: 50px;
}

.periodic-element-correct {
  background-color: green;
}

.periodic-element-incorrect {
  background-color: red;
}

.periodic-element-hidden {
  visibility: hidden;
}

.periodic-element-symbol {
  font-size: 11px;
}

.periodic-element-name {
  font-size: 10px;
}

/* Game board */

.game-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 10px;
  /* background-color: goldenrod; */
}

.game-periodic-container {
  display: -ms-flexbox;
  display: flex;
  /* background-color: goldenrod; */
  -ms-flex-align: center;
      align-items: center;
}

.game-hud-container {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 10px;
  /* background-color: greenyellow; */
  -ms-flex-align: center;
      align-items: center;
}

.game-current-element {
  height: 200px;
  border: 1px solid black;
  padding: 10px;
  width: 100px;
}

.game-current-element-symbol {
  font-size: 40px;
}
.game-current-element-name {
  font-size: 20px;
}

.game-action-button {
  margin: 5px;
  padding: 5px 32px;
  color: white;
  font-size: 16px;
  display: inline-block;
  background-color: #4caf50;
}

/* Score */

.game-score {
  font-size: 60px;
}



